"use client";

import {
  ProviderMonitoringData,
  UsageMetrics,
  HealthStatus,
} from "../../types/llm-providers";

export interface MonitoringConfig {
  healthCheckInterval: number;
  usageUpdateInterval: number;
  alertThresholds: {
    errorRatePercent: number;
    latencyMs: number;
    costPerHour: number;
    consecutiveFailures: number;
  };
  retentionPeriod: number; // hours
}

export interface AlertEvent {
  id: string;
  providerId: string;
  type: "error_rate" | "latency" | "cost" | "health" | "rate_limit";
  severity: "low" | "medium" | "high" | "critical";
  message: string;
  data: any;
  timestamp: Date;
  acknowledged: boolean;
}

/**
 * Real-time provider monitoring service
 */
export class ProviderMonitoringService {
  private static instance: ProviderMonitoringService;
  private providers: Map<string, ProviderMonitoringData> = new Map();
  private alerts: AlertEvent[] = [];
  private listeners: Set<(data: ProviderMonitoringData[]) => void> = new Set();
  private alertListeners: Set<(alert: AlertEvent) => void> = new Set();
  private isRunning = false;
  private healthCheckTimer?: NodeJS.Timeout;
  private usageUpdateTimer?: NodeJS.Timeout;

  private config: MonitoringConfig = {
    healthCheckInterval: 60000, // 1 minute
    usageUpdateInterval: 30000, // 30 seconds
    alertThresholds: {
      errorRatePercent: 5,
      latencyMs: 5000,
      costPerHour: 10,
      consecutiveFailures: 3,
    },
    retentionPeriod: 24, // 24 hours
  };

  private constructor() {}

  static getInstance(): ProviderMonitoringService {
    if (!ProviderMonitoringService.instance) {
      ProviderMonitoringService.instance = new ProviderMonitoringService();
    }
    return ProviderMonitoringService.instance;
  }

  /**
   * Initialize monitoring for providers
   */
  async initialize(
    providers: ProviderMonitoringData[],
    config?: Partial<MonitoringConfig>,
  ) {
    if (config) {
      this.config = { ...this.config, ...config };
    }

    // Initialize provider data
    providers.forEach((provider) => {
      this.providers.set(provider.providerId, {
        ...provider,
        timestamp: new Date(),
      });
    });

    console.log(
      `[MONITORING] Initialized monitoring for ${providers.length} providers`,
    );
    return this.start();
  }

  /**
   * Start real-time monitoring
   */
  async start() {
    if (this.isRunning) {
      console.warn("[MONITORING] Service already running");
      return;
    }

    this.isRunning = true;
    console.log("[MONITORING] Starting real-time monitoring service");

    // Start health checks
    this.healthCheckTimer = setInterval(
      () => this.performHealthChecks(),
      this.config.healthCheckInterval,
    );

    // Start usage updates
    this.usageUpdateTimer = setInterval(
      () => this.updateUsageMetrics(),
      this.config.usageUpdateInterval,
    );

    // Initial health check
    await this.performHealthChecks();
  }

  /**
   * Stop monitoring service
   */
  stop() {
    if (!this.isRunning) return;

    this.isRunning = false;
    console.log("[MONITORING] Stopping monitoring service");

    if (this.healthCheckTimer) {
      clearInterval(this.healthCheckTimer);
      this.healthCheckTimer = undefined;
    }

    if (this.usageUpdateTimer) {
      clearInterval(this.usageUpdateTimer);
      this.usageUpdateTimer = undefined;
    }
  }

  /**
   * Subscribe to provider data updates
   */
  subscribe(listener: (data: ProviderMonitoringData[]) => void) {
    this.listeners.add(listener);
    return () => this.listeners.delete(listener);
  }

  /**
   * Subscribe to alert events
   */
  subscribeToAlerts(listener: (alert: AlertEvent) => void) {
    this.alertListeners.add(listener);
    return () => this.alertListeners.delete(listener);
  }

  /**
   * Get current provider data
   */
  getProviderData(): ProviderMonitoringData[] {
    return Array.from(this.providers.values());
  }

  /**
   * Get provider by ID
   */
  getProvider(id: string): ProviderMonitoringData | undefined {
    return this.providers.get(id);
  }

  /**
   * Update provider configuration
   */
  updateProvider(id: string, updates: Partial<ProviderMonitoringData>) {
    const provider = this.providers.get(id);
    if (!provider) return false;

    const updatedProvider = { ...provider, ...updates };
    this.providers.set(id, updatedProvider);
    this.notifyListeners();
    return true;
  }

  /**
   * Record a request for usage tracking
   */
  recordRequest(
    providerId: string,
    success: boolean,
    inputTokens: number,
    outputTokens: number,
    latency: number,
    cost: number,
    errorType?: string,
  ) {
    const provider = this.providers.get(providerId);
    if (!provider) return;

    const now = new Date();
    const metrics = provider.metrics;

    // Update usage metrics
    metrics.requestCount += 1;
    metrics.tokenCount += inputTokens + outputTokens;
    metrics.costEstimate += cost;

    if (success) {
      metrics.successRate =
        (metrics.successRate * (metrics.requestCount - 1) + 100) /
        metrics.requestCount;
    } else {
      metrics.successRate =
        (metrics.successRate * (metrics.requestCount - 1)) /
        metrics.requestCount;
    }

    // Update averages
    metrics.averageLatency =
      (metrics.averageLatency * (metrics.requestCount - 1) + latency) /
      metrics.requestCount;
    metrics.errorRate = 100 - metrics.successRate;

    // Update timestamp
    provider.timestamp = now;

    if (!success) {
      // Add an incident for the failure
      provider.health.incidents.push({
        id: `incident_${Date.now()}`,
        type: "error",
        severity: "medium",
        startTime: now,
        impact: "Request failed",
        duration: 0,
      });
    }

    // Clean old incidents data
    this.cleanIncidentData(provider);

    // Check for alerts
    this.checkAlerts(provider);

    // Update provider
    this.providers.set(providerId, provider);
    this.notifyListeners();
  }

  /**
   * Perform health checks on all providers
   */
  private async performHealthChecks() {
    const promises = Array.from(this.providers.values()).map((provider) =>
      this.performProviderHealthCheck(provider),
    );

    await Promise.allSettled(promises);
    this.notifyListeners();
  }

  /**
   * Perform health check for a single provider
   */
  private async performProviderHealthCheck(provider: ProviderMonitoringData) {
    const startTime = Date.now();

    try {
      // Call health check API
      const response = await fetch("/api/llm/health-check", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          providerId: provider.providerId,
          providerType: "unknown",
        }),
      });

      const latency = Date.now() - startTime;
      const isHealthy = response.ok;

      // Update health status
      provider.health = {
        status: isHealthy ? "healthy" : "unhealthy",
        lastCheck: new Date(),
        uptime: isHealthy
          ? Math.min(provider.health.uptime + 0.1, 100)
          : Math.max(provider.health.uptime - 1, 0),
        incidents: provider.health.incidents || [],
      };

      console.log(
        `[MONITORING] Health check for ${provider.providerId}: ${provider.health.status} (${latency}ms)`,
      );

      // Check for health alerts
      this.checkHealthAlerts(provider);
    } catch (error) {
      console.error(
        `[MONITORING] Health check failed for ${provider.providerId}:`,
        error,
      );

      provider.health = {
        status: "unhealthy",
        lastCheck: new Date(),
        uptime: Math.max(provider.health.uptime - 5, 0),
        incidents: provider.health.incidents || [],
      };

      this.createAlert(
        provider.providerId,
        "health",
        "high",
        `Provider ${provider.providerId} health check failed`,
        { error: error.message },
      );
    }

    this.providers.set(provider.providerId, provider);
  }

  /**
   * Update usage metrics (calculate rates, etc.)
   */
  private updateUsageMetrics() {
    const now = new Date();
    const oneMinuteAgo = new Date(now.getTime() - 60000);
    const oneHourAgo = new Date(now.getTime() - 3600000);

    this.providers.forEach((provider, id) => {
      // Update timestamp
      provider.timestamp = now;
      this.providers.set(id, provider);
    });

    this.notifyListeners();
  }

  /**
   * Clean old incident data based on retention period
   */
  private cleanIncidentData(provider: ProviderMonitoringData) {
    const cutoff = new Date(Date.now() - this.config.retentionPeriod * 3600000);

    provider.health.incidents = provider.health.incidents.filter(
      (incident) => incident.startTime >= cutoff,
    );
  }

  /**
   * Check for usage-based alerts
   */
  private checkAlerts(provider: ProviderMonitoringData) {
    const { alertThresholds } = this.config;

    // Error rate alert
    if (provider.metrics.errorRate > alertThresholds.errorRatePercent) {
      this.createAlert(
        provider.providerId,
        "error_rate",
        provider.metrics.errorRate > alertThresholds.errorRatePercent * 2
          ? "critical"
          : "high",
        `High error rate: ${provider.metrics.errorRate.toFixed(1)}%`,
        {
          errorRate: provider.metrics.errorRate,
          threshold: alertThresholds.errorRatePercent,
        },
      );
    }

    // Latency alert
    if (provider.metrics.averageLatency > alertThresholds.latencyMs) {
      this.createAlert(
        provider.providerId,
        "latency",
        "medium",
        `High latency: ${provider.metrics.averageLatency.toFixed(0)}ms`,
        {
          latency: provider.metrics.averageLatency,
          threshold: alertThresholds.latencyMs,
        },
      );
    }

    // Cost alert
    if (provider.metrics.costEstimate > alertThresholds.costPerHour) {
      this.createAlert(
        provider.providerId,
        "cost",
        "medium",
        `High cost: $${provider.metrics.costEstimate.toFixed(2)}/hour`,
        {
          costPerHour: provider.metrics.costEstimate,
          threshold: alertThresholds.costPerHour,
        },
      );
    }
  }

  /**
   * Check for health-based alerts
   */
  private checkHealthAlerts(provider: ProviderMonitoringData) {
    const { alertThresholds } = this.config;

    // Health status alert
    if (provider.health.status === "unhealthy") {
      this.createAlert(
        provider.providerId,
        "health",
        "critical",
        `Provider is unhealthy`,
        { status: provider.health.status },
      );
    }

    // Low uptime alert
    if (provider.health.uptime < 95) {
      this.createAlert(
        provider.providerId,
        "health",
        "high",
        `Low uptime: ${provider.health.uptime.toFixed(1)}%`,
        { uptime: provider.health.uptime },
      );
    }
  }

  /**
   * Create an alert
   */
  private createAlert(
    providerId: string,
    type: AlertEvent["type"],
    severity: AlertEvent["severity"],
    message: string,
    data: any,
  ) {
    // Check if similar alert already exists (prevent spam)
    const recentSimilarAlert = this.alerts.find(
      (alert) =>
        alert.providerId === providerId &&
        alert.type === type &&
        alert.severity === severity &&
        Date.now() - alert.timestamp.getTime() < 300000, // 5 minutes
    );

    if (recentSimilarAlert) return;

    const alert: AlertEvent = {
      id: `alert-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
      providerId,
      type,
      severity,
      message,
      data,
      timestamp: new Date(),
      acknowledged: false,
    };

    this.alerts.push(alert);
    console.warn(`[MONITORING] Alert created: ${alert.message}`);

    // Notify alert listeners
    this.alertListeners.forEach((listener) => listener(alert));

    // Clean old alerts (keep last 100)
    if (this.alerts.length > 100) {
      this.alerts = this.alerts.slice(-100);
    }
  }

  /**
   * Get all alerts
   */
  getAlerts(): AlertEvent[] {
    return [...this.alerts];
  }

  /**
   * Acknowledge an alert
   */
  acknowledgeAlert(alertId: string) {
    const alert = this.alerts.find((a) => a.id === alertId);
    if (alert) {
      alert.acknowledged = true;
    }
  }

  /**
   * Get monitoring statistics
   */
  getStats() {
    const providers = Array.from(this.providers.values());
    const totalRequests = providers.reduce(
      (sum, p) => sum + p.metrics.requestCount,
      0,
    );
    const totalCost = providers.reduce(
      (sum, p) => sum + p.metrics.costEstimate,
      0,
    );
    const healthyProviders = providers.filter(
      (p) => p.health.status === "healthy",
    ).length;
    const activeAlerts = this.alerts.filter((a) => !a.acknowledged).length;

    return {
      totalProviders: providers.length,
      healthyProviders,
      totalRequests,
      totalCost,
      activeAlerts,
      isRunning: this.isRunning,
    };
  }

  /**
   * Notify all listeners of data changes
   */
  private notifyListeners() {
    const data = this.getProviderData();
    this.listeners.forEach((listener) => listener(data));
  }

  /**
   * Export monitoring data for analysis
   */
  exportData(timeRange?: { start: Date; end: Date }) {
    const providers = this.getProviderData();

    return {
      providers: providers.filter(
        (provider) =>
          !timeRange ||
          (provider.timestamp >= timeRange.start &&
            provider.timestamp <= timeRange.end),
      ),
      alerts: this.alerts.filter(
        (alert) =>
          !timeRange ||
          (alert.timestamp >= timeRange.start &&
            alert.timestamp <= timeRange.end),
      ),
      exportedAt: new Date(),
      timeRange,
    };
  }
}
